import React, { useCallback, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import styles from './SignUp.module.css';
import loginStyles from './Login.module.css';
import axios from 'axios';

// Button 컴포넌트 import
import Button from 'react-bootstrap/Button';

const SignUp = () => {
  const navigate = useNavigate();

  // useState 수정
  const [msg, setMsg] = useState('');
  const [formData, setFormData] = useState({
    userId: '',
    userPw: '',
  });
  const { userId, userPw } = formData;

  // 사용자 입력 값을 폼 데이터에 업데이트
  const handleChange = useCallback((e) => {
    const { name, value } = e.target;
    setFormData((prevFormData) => ({
      ...prevFormData,
      [name]: value,
    }));
  }, []);

  const handleSubmit = async (e) => {
    e.preventDefault();

    try {
      const res = await axios.post('/signup', formData);

      if (res.status !== 200) {
        // 서버가 요청을 제대로 처리하지 못하면
        throw new Error('회원가입 실패');
      }

      alert('회원가입 성공');
      navigate('/');
    } catch (err) {
      // setMsg 사용하여 오류 메시지 저장
      setMsg(err.message);
    }
    console.log('formData : ', formData);
  };

  const handleCancel = () => {
    navigate('/');
  };

  return (
    <div className={styles.page}>
      <header className={styles.header}>
        <h2 className={styles.signUptitle}>회원가입</h2>
      </header>
      <div className={styles.InputWrapper}>
        <label>
          <div className={styles.signupList}>아이디</div>
          <div className={styles.idBox}>
            <input
              type="text"
              placeholder="영문, 숫자 4자 이상"
              value={userId}
              name="userId"
              onChange={handleChange}
              className={loginStyles.input}
            />
          </div>
        </label>
        <br />
        <label>
          <div className={styles.signupList}>비밀번호</div>
          <input
            type="password"
            placeholder="영문, 숫자, 특수문자 포함 8~20자"
            value={userPw}
            name="userPw"
            onChange={handleChange}
            className={loginStyles.input}
          />
        </label>
      </div>
      <br />

      <div className={styles.signupBtnWrapper}>
        <Button type="submit" onClick={handleSubmit} className={styles.signupBtn}>
          회원가입
        </Button>
        <Button type="submit" onClick={handleCancel} className={styles.signupBtn}>
          취소
        </Button>
      </div>
      {/* 오류 메시지 출력 부분 추가 */}
      {msg && <p>{msg}</p>}
    </div>
  );
};

export default SignUp;
